/* visibility helpers */
.hide {
  display: none !important; }

.show {
  display: block !important; }

.invisible {
  visibility: hidden !important; }

.visible {
  visibility: visible !important; }

.srOnly {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0, 0, 0, 0); }

.srOnly--focusable:active,
.srOnly--focusable:focus {
  position: static;
  overflow: visible;
  width: auto;
  height: auto;
  margin: 0;
  clip: auto; }

.printOnly {
  display: none !important; }

/* print helpers */
@media print {
  .printHide {
    display: none !important; }
  .printOnly {
    display: block !important; }
  table.printOnly {
    display: table !important; }
  thead.printOnly {
    display: table-header-group !important; }
  tbody.printOnly {
    display: table-row-group !important; }
  tr.printOnly {
    display: table-row !important; }
  td.printOnly {
    display: table-cell !important; }
  th.printOnly {
    display: table-cell !important; } }
